.poster{
    display: flex;
    width: 87%;
    height: 62vh;
    margin: 10px auto;
    background-image: url(./img/spase.jpg) ;
    background-position:center;
    background-repeat: no-repeat;
    background-size: cover;
    border: 3px solid greenyellow;
}
.poster img {
    width: 33vh;
    height: 50vh;
    border: 4px solid #151bd1;
    margin: 4%;
}
.description{
    width: 50%;
    height: 50vh;
/* /border: 1px solid white; */
    margin-top: 5%;
    margin-left: 7%;
}

table{
    color: white;
    border-collapse: collapse;
  border: 2px solid rgb(140 140 140);
  font-family: sans-serif;
  font-size: 0.8rem;
  /* letter-spacing: 1px; */
  max-width: 593px;
  max-height: 480px;

}
th,
td {
  border: 1px solid rgb(160 160 160);
  padding: 8px 10px;
}
td:last-of-type {
    text-align: center;
    line-height: 1.63;
  }
  
  td:last-of-type {
    text-align: justify;
}